.video-callback {
	position: relative;
	padding-top: 165px;
	padding-bottom: 145px;
	.video-callback_title {
		.courses_title();
	}

}


.video-callback_slider .video-callback_slide {
	background: url(../img/marcus-p-538404-unsplash.jpg) no-repeat center;
	background-size: cover;
	// padding-top: 35%;
	position: relative;
	display: flex !important;
	iframe {
	position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: none;
  
	}

	&:after {
		content: "";
		padding-top: 25%;
	}	
}		


.nexttt, .prevvv {
	width: 80px;
	height: 80px;
	background: @main_color;
	position: absolute;
	top: 56%;
	z-index: 15;
	img {
		position: absolute;
		top: 32px;

	}

}

.nexttt {
	right: -40px;
	border-top-left-radius: 50%;
	border-bottom-left-radius: 50%;
	transition: 0.4s all ease;
	&:hover {
		right: 0px;
	}

	img {
		margin-left: 26px;
	}

}

.prevvv {
	left: -40px;
	border-top-right-radius: 50%;
	border-bottom-right-radius: 50%;
	transition: 0.4s all ease;
	&:hover {
		left: 0px;
	}
	img {
		margin-left: 43px;
	}
}


