.video-callback_slide {
	.video_circle-wrapper {
		margin: auto;
		display: inline-block;
		.video_circle-wrap();
	}	
}


.video-callback_slider {
	.slick-dots {
		bottom: -50px;
		li {
			 button:before {
			 	font-size: 36px;
			 	color: @main_color;
			 }
		}
	}
}