@import url('app/less/_normalize.less');
@import url('app/less/_fonts.less');
@import url('app/less/_variables.less');

html, body, section {
	overflow-x: hidden; 

}
body 
{
	margin: 0;
	padding: 0;
	font-family: 'ApercuPro';
	font-size: @main_fs;
	color: @font_color;	
	font-weight: 200;



}

div, a {
	outline: none;
}



*, *:before, *:after {
	box-sizing: border-box;
}

img
{
	max-width: 100%;
	display: block;
}

h1, h2, h3, h4, h5, h6, p {
	padding: 0;
	margin: 0;
}

h2 {
	font-size: @h2_header;
	font-family: 'PragmaticaSlab', sans-serif;
	font-weight: @fw;
	overflow-wrap: break-word;
}


ul , li {
	list-style: none;
	padding: 0;
	margin: 0;
}

a {
	text-decoration: none;
	padding: 0;
	margin: 0;
	display: block;
}


input {
	width: 100%;
	outline: none;
	border: none;
	font-family:  sans-serif;
	text-align: center;
}


.center {
	max-width: 1210px;
	width: 100%;
	margin: 0 auto;
	padding: 0 20px;

}

.flex-container {

	display: flex;
	justify-content: space-between;
	align-items: center;
	flex-wrap: wrap;

	flex-direction: row;
}

.inline_wrapper {
	text-align: center;
}

.header_contact, .header_nav, .header_nav-list {
	width: 100%;
	display: flex;
	justify-content: space-between;
	align-items: center;
	flex-wrap: nowrap;
	
}


.header_nav {
	font-weight: @fwm;
	padding: 15px 30px;
}
.header_nav-list {
	justify-content: space-around;
	.header_nav-listItem a{
		color:@font_color ;
	}
}

.header_contact {
	padding: 30px 0;
	position: relative;
		div {
			cursor: pointer;
		}
		&:after {
		content: "";
		position: absolute;
		bottom: 0;
		width: 100%;
		left: 0;
		height: 1px;
		background: #000000;
		transform: scaleX(25);
		opacity: 0.2;
	}
	
}


.header_nav {
	padding: 15px 110px;
	.header_nav-listItem {
		margin-right: 15px;
		line-height: 25px;
		&:hover {
			a{
				color: @main_color;
				transition: all 0.2s linear;
			}
		}
	}
}

.header_nav-main {
	width: 100%;
}


.header_contact_logo {
	order: 1;
	display: flex;
	justify-content: space-between;
	align-items: center;
	flex-wrap: nowrap;
	.logo-name {
		font-size: @smallest_fs;
		font-weight: @fwm;
		margin-left: 28px;
	}
}


.header_contact-btn {
	order: 3;
	border: 1px solid @main_color;
	padding: 15px 70px 15px 40px;
	border-radius: 25px;
	text-align: center;
	position: relative;
	font-size: 18px;
	&:before {
		position: absolute;
    content: "";
    width: 21px;
    height: 15px;
    background: url(../img/right-arrow.png) 0 0/cover no-repeat;
    right: 30px;
    top: 20px;

	}

	div {
		color: @main_color;
	}
	
}

.header_contact-call {
	order: 5;
	background: @main_color;
	    padding: 47px 47px;
    margin: -30px 0;
    font-size: 16px;
	div {
		color: @white_color;
	}
}

.header_contact_city {
	font-size:@smallest_fs;
}


.header_contact-tel {
	order: 4;
	position: relative;
	.header_contact-cities {
		position: absolute;
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 35px 25px;
		background: @white_color;
		border-radius: 25px;
		z-index: 10;
		top: 0;
		left: -57px;
		display: none;
		box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
		ul {
			margin: 0 20px;
		}
		li {
			cursor: pointer;
			transition: 0.2s linear;
			margin-bottom: 	25px;
			&:hover {
				color: @main_color;
			}
		}

	}
}


.my_slider{
	max-width: 1170px;
	margin: 0 auto;
}

.next, .prev {
	width: 80px;
	height: 80px;
	background: @main_color;
	position: absolute;
	top: 35%;
	z-index: 15;
	img {
		position: absolute;
		top: 32px;

	}

}

.next {
	right: -40px;
	border-top-left-radius: 50%;
	border-bottom-left-radius: 50%;
	transition: 0.4s all ease;
	&:hover {
		right: 0px;
	}

	img {
		margin-left: 26px;
	}

}

.prev {
	left: -40px;
	border-top-right-radius: 50%;
	border-bottom-right-radius: 50%;
	transition: 0.4s all ease;
	&:hover {
		left: 0px;
	}
	img {
		margin-left: 43px;
	}
}


.wrapper_title {
	text-align: center;
	margin-bottom: 90px; 
	position: relative;
		display: inline-block;
		&:before {
		content: "";
		position: absolute;
		top: 50%;
		left: -70px;
		background: rgb(25, 109, 185);
		width: 25px;
		height: 2px;

	}
	&:after {
		content: "";
		position: absolute;
		top: 50%;
		right: -70px;
		background: rgb(25, 109, 185);
		width: 25px;
		height: 2px;
	}
}

.header_contact {
	position: relative;
}

.header_mobile-menu-wrap {
	// position: absolute;
	order: 2;
	width: 60px;
	height: 60px;
	right: 0;
	top: 22%;
	margin: 0;
	display: flex;
	justify-content: center;
	align-items: center;
	display: none;
	&:hover {
		span {
			background: @main_color;
			&:before, &:after {
				background: @main_color;
			}
		}
	}

	span {
		display: block;
		transition: 0.2s linear;
		width: 30px;
		height: 2px;
		background: @font_color;
		border-radius: 5px;
		position: relative;
		&:before, &:after {
			position: absolute;
			transition: 0.2s linear;
			content: "";
			width: 100%;
			height: 100%;
			background: @font_color;
			

		}
		&:after {
			bottom: 7px;
		}

		&:before {
			top: 7px;
		}
	}

}


.active_btn {
	opacity: 1 !important;
	pointer-events: all !important;
}

.logo_wrap {
	display: flex;
	align-items: center;
	justify-content: space-between;
	flex-wrap: nowrap;
}
.header_contact-tel {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-wrap: nowrap;
}

.header_contact-phone {
	margin-left: 45px;
}

.header_contact_city {
	display: inline;
		position: relative;
		 &:hover:before {
    	 border-top: 5px solid @font_color;
    	 }
		&:before {
			content: "";
			position: absolute;
	
    border: 5px solid transparent; /*   */
    border-top: 5px solid #e7e7e7; /*   */
        right: -21px;
        transition: 0.8s all;
    top: 8px;
   
		}
    
	}
// POPUP


/* Styles for dialog window */
#small-dialog {
	background: white;
	padding: 20px 30px;
	text-align: left;
	max-width: 400px;
	margin: 40px auto;
	position: relative;
}


/**
 * Fade-zoom animation for first dialog
 */

/* start state */
.my-mfp-zoom-in .zoom-anim-dialog {
	opacity: 0;

	-webkit-transition: all 0.2s ease-in-out; 
	-moz-transition: all 0.2s ease-in-out; 
	-o-transition: all 0.2s ease-in-out; 
	transition: all 0.2s ease-in-out; 



	-webkit-transform: scale(0.8); 
	-moz-transform: scale(0.8); 
	-ms-transform: scale(0.8); 
	-o-transform: scale(0.8); 
	transform: scale(0.8); 
}

/* animate in */
.my-mfp-zoom-in.mfp-ready .zoom-anim-dialog {
	opacity: 1;

	-webkit-transform: scale(1); 
	-moz-transform: scale(1); 
	-ms-transform: scale(1); 
	-o-transform: scale(1); 
	transform: scale(1); 
}

/* animate out */
.my-mfp-zoom-in.mfp-removing .zoom-anim-dialog {
	-webkit-transform: scale(0.8); 
	-moz-transform: scale(0.8); 
	-ms-transform: scale(0.8); 
	-o-transform: scale(0.8); 
	transform: scale(0.8); 

	opacity: 0;
}

/* Dark overlay, start state */
.my-mfp-zoom-in.mfp-bg {
	opacity: 0;
	-webkit-transition: opacity 0.3s ease-out; 
	-moz-transition: opacity 0.3s ease-out; 
	-o-transition: opacity 0.3s ease-out; 
	transition: opacity 0.3s ease-out;
}
/* animate in */
.my-mfp-zoom-in.mfp-ready.mfp-bg {
	opacity: 1;
}
/* animate out */
.my-mfp-zoom-in.mfp-removing.mfp-bg {
	opacity: 0;
}

.mfp-close-btn-in .mfp-close {
	display: none;
}




/**
 * Fade-move animation for second dialog
 */

/* at start */
.my-mfp-slide-bottom .zoom-anim-dialog {
	opacity: 0;
	-webkit-transition: all 0.2s ease-out;
	-moz-transition: all 0.2s ease-out;
	-o-transition: all 0.2s ease-out;
	transition: all 0.2s ease-out;

	-webkit-transform: translateY(-20px) perspective( 600px ) rotateX( 10deg );
	-moz-transform: translateY(-20px) perspective( 600px ) rotateX( 10deg );
	-ms-transform: translateY(-20px) perspective( 600px ) rotateX( 10deg );
	-o-transform: translateY(-20px) perspective( 600px ) rotateX( 10deg );
	transform: translateY(-20px) perspective( 600px ) rotateX( 10deg );

}

/* animate in */
.my-mfp-slide-bottom.mfp-ready .zoom-anim-dialog {
	opacity: 1;
	-webkit-transform: translateY(0) perspective( 600px ) rotateX( 0 ); 
	-moz-transform: translateY(0) perspective( 600px ) rotateX( 0 ); 
	-ms-transform: translateY(0) perspective( 600px ) rotateX( 0 ); 
	-o-transform: translateY(0) perspective( 600px ) rotateX( 0 ); 
	transform: translateY(0) perspective( 600px ) rotateX( 0 ); 
}

/* animate out */
.my-mfp-slide-bottom.mfp-removing .zoom-anim-dialog {
	opacity: 0;

	-webkit-transform: translateY(-10px) perspective( 600px ) rotateX( 10deg ); 
	-moz-transform: translateY(-10px) perspective( 600px ) rotateX( 10deg ); 
	-ms-transform: translateY(-10px) perspective( 600px ) rotateX( 10deg ); 
	-o-transform: translateY(-10px) perspective( 600px ) rotateX( 10deg ); 
	transform: translateY(-10px) perspective( 600px ) rotateX( 10deg ); 
}

/* Dark overlay, start state */
.my-mfp-slide-bottom.mfp-bg {
	opacity: 0;

	-webkit-transition: opacity 0.3s ease-out; 
	-moz-transition: opacity 0.3s ease-out; 
	-o-transition: opacity 0.3s ease-out; 
	transition: opacity 0.3s ease-out;
}
/* animate in */
.my-mfp-slide-bottom.mfp-ready.mfp-bg {
	opacity: 0.8;
}
/* animate out */
.my-mfp-slide-bottom.mfp-removing.mfp-bg {
	opacity: 0;
}




