.questions_title {
	.courses_title();
}

.questions{
	text-align: center;
	padding-top: 145px;
	padding-bottom: 145px;
	.wrapper_title {
		margin-bottom: 40px;
	}
}

.questions_form {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-wrap: nowrap;
	width: 100%;
	margin: 0 auto;
	margin-bottom: 40px;
}

.questions_form-title {
	text-align: center;
	width: 100%;
	margin-bottom: 70px;
}

.question_agreement {
	width: 100%;
	a {
		color: @main_color;
		display: inline-block;
	}
	font-size: 14px;
}

.questions_btn{
	.header_contact-btn();
	background: @white_color;
	max-width: 206px;
	text-align: left;
	color: @main_color;


	
}


.questions_field {
	border-radius: 25px;
	border: 1px solid #e9e9e9;
	    padding: 15px 25px;
	    text-align: left;
	    max-width: 375px;
	    margin-right: 30px;
	    font-weight: 200;
}


.submit_wrap {
		position: relative;
		max-height: 47px;
	&:before {
		position: absolute;
    content: "";
    width: 21px;
    height: 15px;
    background: url(../img/right-arrow.png) 0 0/cover no-repeat;
    right: 30px;
    top: 20px;
    z-index: 3;

	}
}
