.teachers {
	padding-top: 164px;
	background: #f2f3f3;
	padding-bottom: 145px;
	position: relative;
}

.teachers_title {
	.courses_title();		
}




.teacher_content {
	flex: 5;
}

.teacher_photo {
	flex: 1;
	margin-right: 	10px;
	
		img {
			width: 107px;
			height: 107px;
			max-width: 107px;
		}
	
}

.teacher_name {
	.callback_slide-title();
}

.teacher_bio {
	margin-bottom: 	45px;
	letter-spacing: -0.46px;
	line-height: 30px;
	span{
		display: block;
		font-weight: 500;
		line-height: 36px;
	}
}

.teachear_exp {
	position: relative;
	&:before {
		position: absolute;
		content: "";
		width: 125px;
		height: 125px;
		background: #ebf4fc;
		border-radius: 50%;
		z-index: 0;
		right: 46%;
		top: -45%;
	}
	p {
		display: block;
		position: relative;
		padding-left: 35px;
		margin-bottom: 	30px;
		&:before {
			position: absolute;
			content: "";
			top: 0;
			left: 0;
			background: url(../img/mortarboard.png) no-repeat center;
			background-size: cover;
			width: 23px;
			height: 23px;

		}
		&:nth-child(2):before {
			background: url(../img/student.png) no-repeat center;
			background-size: cover;
		}
	}
}




.teachers_slider .teachers_slide.my_slide{
	display: flex !important;
	justify-content: space-between;
	align-items: stretch;
	flex-wrap: nowrap;
	flex-flow: row;
	padding: 80px 70px;
	background: @white_color;
	padding-bottom: 60px;
	

}





.nextttt, .prevvvv {
	width: 80px;
	height: 80px;
	background: @main_color;
	position: absolute;
	top: 57%;
	z-index: 15;
	img {
		position: absolute;
		top: 32px;

	}

}

.nextttt {
	right: -40px;
	border-top-left-radius: 50%;
	border-bottom-left-radius: 50%;
	transition: 0.4s all ease;
	&:hover {
		right: 0px;
	}

	img {
		margin-left: 26px;
	}

}

.prevvvv {
	left: -40px;
	border-top-right-radius: 50%;
	border-bottom-right-radius: 50%;
	transition: 0.4s all ease;
	&:hover {
		left: 0px;
	}
	img {
		margin-left: 43px;
	}
}