.video_link {
	display: flex;
	width: 100%;
	height: 100%;
	justify-content: center;
	align-items: center;
	position: relative;
	z-index: 10;

	&:after {
		content: "";
		padding-top: 42%;
		display: block;
	}
}



.video_circle {
	animation: rot 7s linear infinite;
}
.video_circle-wrap{
	margin: auto;
	width: 126px;
	height: 126px;
	display: inline-block;
	position: relative;

	&:hover {

		&:after{
			transform: scale(1.2) translate(115%, -45%) rotate(270deg);;
		}
	}
	
	z-index: 5;
	&:before {
	content: "";
	position: absolute;
	animation: none;
	    top: 27px;
    left: 28px;
    width: 57%;
    height: 57%;
	background: transparent;
	border-radius: 50%;
	border: 3px solid @white_color;
	z-index: 20;	
	}

	&:after {
		transition: all 0.3s linear;
	 content: ''; 
    position: absolute; /*   */
    border: 13px solid transparent; /*   */
    border-top: 20px solid @white_color; /*   */
    top: 50%;
    left: 22%;
    transform: translate(125%, -50%) rotate(270deg);
    z-index: 21;
	}
}



@keyframes rot {
	0%{
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(360deg);
	}
}

