.prices {
	background: @main_color;
	padding-top: 200px;
	position: relative;
	top: -125px;
	margin-bottom: -125px;

}
.prices_title {
	text-align: center;
	color: @white_color;
	display: inline;
	position: relative;
	&:before, &:after {
		    content: "";
    position: absolute;
    top: 50%;
    background: #196db9;
    width: 25px;
    height: 2px;
	}
	&:before {
		left: -70px;	
	}
	&:after {
		right: -70px;
	}

}

.tabs, .prices_tab-container {
	display: flex;
	justify-content: space-between;
	align-items: stretch;
	flex-wrap: nowrap;
	width: 100%;
}

.prices_tab-content {
	width: 100%;
}

.tabs{
	position: relative;
	&:before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 1px;
		background: rgba(255,255,255,0.3);
		transform: scaleX(25);

	}
	li {
		flex: 1 0 120px;
		text-align: center;
		padding: 30px 0;
		border: 1px solid rgba(255,255,255,0.3);
		cursor: pointer;
		transition: 0.3s all;
		&:hover {
			box-shadow: 0 10px 30px rgba(0,0,0,.3);
			transform: translate3d(-.5px,-.1px,0) scale(1);
		}
		&:last-child{
			border-right: 1px solid rgba(255,255,255,0.3);
		}
		a{
			color: @white_color;
			pointer-events: none;
		}

	}
	li.active{
		background: @white_color;
		a{
			color: @font_color;
		}
	}
}

.prices_tab-layer {
	background: url(../img/Biology-background.jpg) no-repeat center;
	background-size: cover; 
	background-attachment: fixed;
	padding: 5% 0;
}


