.zoom-anim-dialog {
	.center {
		background: @white_color;
		max-width: 1200px;
		border-radius: 10px;
		font-weight: 700;
		padding: 90px 0;
		.wrapper_title {
			margin-bottom: 40px;
		}
	}

	.popup_title {
		.courses_title();
	}

	.popup_table {
		display: flex;
		justify-content: space-between;
		align-items: center;
		flex-direction: column;
		border-bottom: 1px solid rgba(51,142,255, 0.3);
		border-right: 1px solid rgba(51,142,255, 0.3);
		
		width: 100%;
		font-family: 'PT Sans Caption', sans-serif;
	
	}

	.popup_table-row {
		border-top: 1px solid rgba(51,142,255, 0.3);
		width: 100%;
		display: flex;
		align-items: stretch;
		justify-content: space-between;
		p{
			display: table-cell;
			vertical-align: middle;
			padding: 30px 5px;
			overflow-wrap: break-word;
	
			text-align: center;
			border-left: 1px solid rgba(51,142,255, 0.3);
		}
	}
}

p.popup_num {
	// max-width: 75px;
	background: #d6e8f9;
	width: 10%;
}

.popup_name {
	width: 25%;
}

.popup-ball_before {
	width: 25%;
}

.popup-ball_after {
	width: 20%;
}

.popup_teacher {
	width: 20%;
}



.mfp-bg.my-mfp-zoom-in, .mfp-bg.mfp-ready {
	background: rgba(51,142,255, 0.85);
	filter: blur(74px);
}

.my-mfp-zoom-out {
	position: fixed;
	button.mfp-arrow, button.mfp-close {
		display: none !important;
	}
}


.mfp-bg.my-mfp-zoom-out {
	position: fixed;
}

.popup-ball_after {
	color: @main_color;
}

.popup-ball_before:last-child, .popup-ball_after:last-child {
	background: #d6e8f9;
}


 .thanks_popup, .callback_popup {
	
		max-width: 840px;
		margin: 0 auto;
	
}

.start_popup {
		max-width: 980px;
		margin: 0 auto;
		text-align: center;
}



.thanks_popup {
	.flex-container {
		text-align: center;
		justify-content: center;
	}
	


}


.table-dialog {
	max-width: 1200px;
	margin: 0 auto;
	button.mfp-arrow, button.mfp-close {
		display: block;
	}
		.popop-btn {
		.header_contact-btn();
		font-weight: 300;
		
		max-width: 270px;
		padding: 15px 52px 15px 40px;
		margin: auto;
		margin-top: 45px;
		background: @main_color;
		color: @white_color;
		&:before {
			background: url(../img/right-arrow.svg) no-repeat center;
			background-size: cover;
		}
	}
	}

	.table-dialog .popop-btn div {
		color: @white_color;
	}



.start_popup {
	.flex-container {
		text-align: center;
		justify-content: center;
	}

	.popop-btn {
		.header_contact-btn();
		font-weight: 300;
		margin-right: 20px;
	}
	
}


.callback_popup {
	.mfp-close-btn-in .mfp-close {
			display: block;

		}
	button.mfp-arrow, button.mfp-close {
		display: block;
	}
	.popup_form {
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
		width: 100%;

		.popop-btn {
			padding: 15px 17px 15px 60px;
			margin-top: 45px;
			cursor: pointer;
		}
	}
}


.popup_logo {
	text-align: center;
	margin: 0 auto;
	display: inline-block;
	margin-bottom: 	25px;

	img {
		margin-right: 	28px;
	}
}