.footer {
	.wrapper_title {
		position: relative;
		z-index: 1;
		&:before {
			content: "";
			position: absolute;
			width: 100%;
			min-width: 320px;
			display: block;
			height: 150%;
			background: @white_color;
			filter: blur(40px);
			margin: 0 auto;
			right: 0;
			top: 0;

		}

			&:after {
		display: none;

		}
	

		z-index: 0;
		top: 0;
		left: 0;
		padding-top: 50px;
	}
	.center {
		padding-top: 125px;
		pointer-events: none;
	}
	padding-bottom: 145px;

	position: relative;
	* {
		position: relative;
		z-index: 1;
	}
}

.footer_title {
	.courses_title();
	position: relative;
	&:after {
    content: "";
    position: absolute;
    top: 50%;
    right: -70px;
    background: #196db9;
    width: 25px;
    height: 2px;
}
	&::before {
    content: "";
    position: absolute;
    top: 50%;
    left: -70px;
    background: #196db9;
    width: 25px;
    height: 2px;
}
	}

		


.google_map {
	position: absolute;
	width: 100%;
	height: 100%;
	z-index: 0;


}


.footer_contacts {
	border-radius: 15px;
	background: @white_color;
	display: flex;
	justify-content: space-between;
	align-items: flex-start;
	flex-direction: column;
	flex-wrap: nowrap;
	padding: 54px 47px;
	max-width: 470px;
	font-weight: 400;
}


.footer-btn	{
	.header_contact-call();
	margin: 0;
	width: 100%;
	color: @white_color;
	text-align: center;
	padding: 27px;
	margin-bottom: 45px;
	pointer-events: auto;
	order: 0;
}

.footer-adress {

	margin-bottom: 50px;
line-height: 30px;
	position: relative;
	padding-left: 40px;
	&:before {
		content: "";
		background: url(../img/placeholder.png) no-repeat;
		width: 24px;
		height: 30px;
		position: absolute;
		background-size: cover;
		top: 0;
    left: 0;
	}
}

.footer-tel {
	margin-bottom: 50px;
	line-height: 30px;
	position: relative;
	padding-left: 40px;
	&:before {
		content: "";
		background: url(../img/telephone.png) no-repeat;
		width: 28px;
		height: 28px;
		position: absolute;
		background-size: cover;
		top: 0;
    left: 0;
	}
}


.footer-social {
	width: 100%;
	display: flex;
	align-items: center;
	flex-wrap: nowrap;
	justify-content: center;
	top: 0;
    left: 0;
    a{
    	margin-right: 	25px;
    }

    img {
    	width: 30px;
    	height: 30px;
    }
}