.courses{
	padding: 45px 0;
	background:#f2f3f3;
	padding-top: 165px;

	* {
		position: relative;
		z-index: 2;
	}

	.wrapper_title {
		margin-bottom: 65px;
		
	}

	.flex-container {
		justify-content: center;
	}
	
}

.courses_title {
	text-align: center;
	position: relative;
	display: inline;
	
}

.courses_card {
	background: @white_color;
	min-width: 240px;
	border-radius: 10px;
	padding: 15px; 
	padding-bottom: 30px;
	transition: all 0.3s linear;
	// margin: 0 auto;
	margin-right: 15px;
	margin-top: 25px;
	&:hover {
		transform: translate3d(-.5px,-.1px,0) scale(1.02);
			box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
	}
	.courses_card-text {
		font-size: @bigger_fs;
		color: @main_color;
		text-align: center;
		margin-top: 20px;
	}
}