.best {
	background-color: @main_color;
	color: @white_color;
	position: relative;
	padding-top: 220px;
	padding-bottom: 145px;
	&:before {
		width: 100%;
		content: "";
		height: 150px;
		background: url(../img/curve-layer.png) no-repeat center;
		background-size: cover;
		top: 0;
		position: absolute;
	}
	.flex-container {
		align-items: stretch;
		flex-wrap: wrap;
		// justify-content: center;
	}
	
}


.best_card {
	margin: 0 15px;
	flex: 1;
	 min-width: 144px;
	text-align: center;
	// justify-content: center;
	// width: 15%;
	// align-self: center;
	// margin-right: 35px;
	&:last-child {
		margin-right: 0;
	}
}


.best_card-descr {
		font-size: @bigger_fs;
font-weight: 400;
line-height: 37.57px;
margin-bottom: 27px;
}

.best_card-img {
	position: relative;

	margin-bottom: 	75px;
	margin-top: 	75px;
	display: flex;
	justify-content: center;
	align-items: center;


	
	&:before {
		content: "";
		position: absolute;
		background-color: @white_color;
		width: 	150px;
		height: 150px;
		border-radius: 50%;
		    top: -65%;
    // left: 18.5%;
		z-index: 1;
	}
	img {
		margin: 0 auto;
		position: relative;
		z-index: 2;
		// min-width: 150px;
	}
}


.best_card-text {
font-weight: 400;
line-height: 30px;
}