.callback {
	padding-top: 164px;
	padding-bottom: 220px;
	position: relative;
	background-color: #f2f3f3;
	.flex-container {
		position: relative;
	}
	.callback_title {
		.courses_title();
	}
}


.nextt, .prevv {
	width: 80px;
	height: 80px;
	background: @main_color;
	position: absolute;
	top: 43%;
	z-index: 15;
	img {
		position: absolute;
		top: 32px;

	}

}

.nextt {
	right: -40px;
	border-top-left-radius: 50%;
	border-bottom-left-radius: 50%;
	transition: 0.4s all ease;
	&:hover {
		right: 0px;
	}

	img {
		margin-left: 26px;
	}

}

.prevv {
	left: -40px;
	border-top-right-radius: 50%;
	border-bottom-right-radius: 50%;
	transition: 0.4s all ease;
	&:hover {
		left: 0px;
	}
	img {
		margin-left: 43px;
	}
}


.callback_slider .callback_slide {
	background: @white_color;
	padding: 0 40px;		
	padding-top: 80px;
	text-align: center;
	padding-bottom: 70px;
}


.callback_slide-title {
	    font-size: 30px;
    font-weight: 400;
    line-height: 59.34px;
    font-family: PragmaticaSlab;
   
}

.callback_slide-name, .callback_slide-useful {

	color: #b0b0b0;
}

.callback_slide-useful {
	font-size: 16px;
	margin-bottom: 17px;
}

.callback_slide-name, .callback_slide-text {
	margin-bottom: 80px;
	line-height: 36px;
}

.callback_slide-mark {
	text-align: center;
	display: flex;
	width: 100%;
	justify-content: center;
	align-items: center;
}

.callback_slide-text {
	position: relative;
	
}


.callback_slide-mark-l {
	margin-right: 15px;
	display: flex;
	span{
		margin-left: 	5px;
	}

}

.callback_slide-mark-r {
	display: flex;
	span {
		margin-left: 5px;
	}
}

.callback_slide-name {
	position: relative;
	 &:before {
		content: "";
		position: absolute;
		width: 8px;
		height: 8px;
	
		background-size: cover; 
		color: #565563;
		right: 50%;

	}
	&:before {
		background: url(../img/left-quote.svg) no-repeat center;
		background-size: cover; 
		bottom: -50px;
	}

}



.callback_slide-useful {
	position: relative;
	&:after {
		content: "";
		position: absolute;
		width: 8px;
		height: 8px;
		background: url(../img/right-quote-sign.svg) no-repeat center;
		background-size: cover; 
		color: #565563;
		right: 50%;
		top: -50px;

	}


}